import moment from "moment";
import React, { Component } from "react";
import { dati } from '../assets/dati';
import PAESI from "../constants/paesi";
import { COMMON_PROPS, DATA_AGGIONAMENTO } from "../constants/conf";

import Grafico from "./Grafico";
import Opzioni from "./Opzioni";
import 'antd/dist/antd.css';
import { Row, Col } from "antd";

let giorni = (range) => {
    let etichette = [];
    let giornoInizio = moment().subtract(-range[0], 'days');

    let dataAggiornamento = moment(DATA_AGGIONAMENTO + " 23:59:ss", "DD/MM/YYYY HH:mm:ss");
    let giornoFinale = dataAggiornamento.subtract(-range[1], 'days');

    while (giornoInizio <= giornoFinale) {
        etichette.push(giornoInizio.format("DD/MM/YYYY"));
        giornoInizio.add(1, 'days');
    }

    return etichette;
}


let recuperaDatasetPaese = (paese, tipo, gg) => {

    let datiPaesePerTipo = gg.map((giorno) => {
        let dato = dati.find((elem) => elem.DateRep === giorno && elem.CountryExp === paese);
        if (dato == null)
            return 0;
        return dato[tipo];

    });


    let ilPaese = PAESI.find(p => p.codice === paese);

    let x = {
        label: ilPaese.descrizione,
        data: datiPaesePerTipo,
        borderColor: ilPaese.borderColor,
        backgroundColor: ilPaese.backgroundColor

    };

    return (
        Object.assign(x, COMMON_PROPS)
    )
}



class Main extends Component {


    constructor(props) {
        super(props);

        this.state = {
            tipo: "SumDeaths",
            range: [-45, 0],
            paesi: ["France", "Spain", "Italy", "United_Kingdom",]
        }

    }


    componentDidMount() {
    }

    ////////////////////////////////////////////////////////////////
    // LIFT UP DELLO STATO
    ////////////////////////////////////////////////////////////////

    liftPaesi = (codice, checked) => {
        console.log(codice + " = " + checked)

        if (checked) {
            this.setState(prevState => ({
                paesi: [codice, ...prevState.paesi]
            }))
        } else {

            this.setState(prevState => {
                const paesi = prevState.paesi.filter(p => p !== codice);
                return { paesi };
            });
          

        }
    }

    liftTipo = (tipo) => {
        this.setState({ "tipo": tipo })
    }

    liftRange = (range) => {
        this.setState({ "range": range })
    }


    ////////////////////////////////////////////////////////////////
    // RICALCOLO
    ////////////////////////////////////////////////////////////////


    ricalcolaGrafico = () => {

        let gg = giorni(this.state.range);
        let newDataSets = this.state.paesi.map(PAESE => recuperaDatasetPaese(PAESE, this.state.tipo, gg));

        return ({
            labels: gg,
            datasets: newDataSets
        });

    }


    ////////////////////////////////////////////////////////////////
    // *** RENDER ***
    ////////////////////////////////////////////////////////////////

    render() {

        return (
            <div>
                <Row>
                    <Col span={24}>
                        <h1 className="titolo">Situazione contagi / decessi per Coronavirus al {DATA_AGGIONAMENTO}</h1>
                    </Col>
                </Row>
                <Row>
                    <Col span={24}>
                        <Opzioni {...this.state}
                            onPaeseChange={this.liftPaesi}
                            onTipoChange={this.liftTipo}
                            onRangeChange={this.liftRange}  ></Opzioni>

                    </Col>
                </Row>
                <Row>
                    <Col span={24}>
                        <div className="grafico">
                            <Grafico data={this.ricalcolaGrafico()}></Grafico>
                        </div>
                    </Col>
                </Row>
                <Row>
                    <Col>
                        <p className="alert">Attenzione! L'applicazione  ancora in  fase di sviluppo per cui sono possibili errori e malfunzionamenti</p>
                    </Col>
                </Row>
                <Row>
                    <Col>
                        <div className="msg">
                            (*) I contagi / decessi del <strong>16 marzo </strong>
                        sono stati registrati SOLO il giorno successivo:
                        ecco perch in tali giorni si vede una perentina quanto anomala discesa
                        seguito da un picco altrettanto improbabile.
                        </div>
                    </Col>
                </Row>
                <Row>
                    <Col>
                        I dati sono stati resi disponibili da:
                    </Col>
                </Row>
                <Row>
                    <Col>
                        <a href="https://www.ecdc.europa.eu/en/publications-data/download-todays-data-geographic-distribution-covid-19-cases-worldwide"
                        >European Centre for Disease Prevention and Control</a>
                    </Col>
                </Row>
                <Row>
                    <Col>
                        Chart CV - Versione 0.6
                        </Col>
                </Row >
                <Row>
                    <Col>
                        <a href="mailto:franco.vuoto@gmail.com">Franco Vuoto</a> - Bologna, 14/03/2020
                        </Col>
                </Row >
            </div >
        );
    }
}

export default Main;

