import React, { Component } from "react";
import { Line } from 'react-chartjs-2';

class Grafico extends Component {

    render() {
        return (
            <div>
                <Line data={this.props.data} width={5} height={400} options={{ maintainAspectRatio: false }} />
            </div>
        );
    }
}

export default Grafico;

