import { Col, Radio, Row, Slider, Switch } from 'antd';
import 'antd/dist/antd.css';
import React, { Component } from "react";
import { DATA_AGGIONAMENTO } from "../constants/conf";
import PAESI from "../constants/paesi";



/***************************************************
 * rendering degli switch degli stati
 ***************************************************/


class Opzioni extends Component {

    componentDidMount() {
    }

    ////////////////////////////////////////////////////////////////
    //// EVENTI DI CONTROLLO
    ////////////////////////////////////////////////////////////////

    handleCheck = nome => (checked, event) => {
        this.props.onPaeseChange(nome, checked);
    };

    handleChangeSelectStati = codiciStati => {
        this.props.onPaeseChange(codiciStati);
    };

    handleChange = (event) => {
        this.props.onTipoChange(event.target.value);
    };

    handleChangeUltimoMese = name => event => {
        this.setState({ ultimoMese: event.target.checked });
    };

    handleSlider = (range) => {
        this.props.onRangeChange(range);
    }

    ////////////////////////////////////////////////////////////////
    //// RENDERING PARZIALI
    ////////////////////////////////////////////////////////////////

    /***************************************************
    * Switch degli stati 
    ***************************************************/

    renderCheck = (codice, descrizione) => {

        return (<Switch checkedChildren={descrizione} key={codice}
            unCheckedChildren={descrizione}
            onChange={this.handleCheck(codice)} checked={this.props.paesi.includes(codice)}
        />)
    };



    /***************************************************
     * etichetta dei tooltip dello slider
     ***************************************************/

    formatTootip = (valore) => {

        let numGiorni = -parseInt(valore);

        if (numGiorni === 0) return DATA_AGGIONAMENTO;
        if (numGiorni === 1) return "1 giorno prima";
        return numGiorni + " giorni prima"

    }


    ////////////////////////////////////////////////////////////////
    //// RENDER
    ////////////////////////////////////////////////////////////////


    render() {


        return (
            <div>
                <Row>
                    <Col span={24}>
                        <fieldset className={"opzfieldset"}>
                            <legend className={"opzlegend"}> Opzioni </legend>


                            <div className="switchPaesi">
                                {PAESI.map(paese => this.renderCheck(paese.codice, paese.descrizione))}
                            </div>

                            <div className="tipoStats">

                                <Radio.Group name="tipo" onChange={this.handleChange}
                                    value={this.props.tipo}
                                    buttonStyle="solid"
                                >
                                    <Radio.Button style={{ "margin": "2px" }} value={"SumDeaths"}  >Totale decessi</Radio.Button>
                                    <Radio.Button style={{ "margin": "2px" }} value={"SumCases"}>Totale contagiati</Radio.Button>
                                    <Radio.Button style={{ "margin": "2px" }} value={"NewDeaths"}>Nuovi decessi</Radio.Button>
                                    <Radio.Button style={{ "margin": "2px" }} value={"NewConfCases"}>Nuovi contagiati</Radio.Button>
                                </Radio.Group>
                            </div>

                            <div className="slider">
                                Periodo di rilevazione:
                                <Slider min={-81} max={0} step={1} range value={this.props.range}
                                   
                                    tipFormatter={this.formatTootip}
                                    onChange={this.handleSlider} />
                            </div>
                        </fieldset>
                    </Col>
                </Row>

            </div>

        );
    }
}

export default Opzioni;

